import React from "react";
import "./PageIndicator.css";

export default function PageIndicator({ counts = 3, current = 1, ...props }) {
	return (
		<div className="page-indicator" {...props}>
			{Array.apply(null, { length: counts }).map((_, i) => (
				<div
					className={
						i === current
							? "page-indicator-dot-selected"
							: "page-indicator-dot-unselected"
					}
				/>
			))}
		</div>
	);
}
