import React, { useEffect, createRef, useState } from "react";
import { Grid, Fade } from "@material-ui/core";
import {
	ExpandMoreOutlined as ScrollDownIcon,
	ExpandLessOutlined as ScrollUpIcon,
} from "@material-ui/icons";
import PageIndicator from "../../components/PageIndicator";
import "../../App.css";
import { CSSTransition, TransitionGroup } from "react-transition-group";
import { useSwipeable } from "react-swipeable";

const req_scroll_delta = 150;

export default function HomePage({ onMouseScrollDown = () => {}, ...props }) {
	const gridContainerRef = createRef();
	let timer = null;
	let deltaYSum = 0;
	const [selectedPage, setSelectedPage] = useState(0);

	const pages = [
		<CSSTransition
			classNames="example"
			timeout={{ enter: 250, leave: 150, appear: 150 }}
			className="home-grid-item"
			key={1}
		>
			<div
				style={{
					width: "100%",
					display: "flex",
					justifyContent: "center",
					alignItems: "center",
					flexDirection: "column",
				}}
			>
				<h1 className="home-welcome">Hello and welcome to my Portfolio</h1>
				<div className="divider" />
				<p className="home-description">Scroll down to see more</p>
			</div>
		</CSSTransition>,
		<CSSTransition
			classNames="example"
			timeout={{ enter: 250, leave: 150, appear: 150 }}
			className="home-grid-item"
			key={2}
		>
			<div
				style={{
					width: "100%",
					display: "flex",
					justifyContent: "center",
					alignItems: "center",
					flexDirection: "column",
				}}
			>
				<h1 className="home-welcome">Currently in development</h1>
				<div className="divider" />
				<p className="home-description">
					This page is currently in development. Come back again in a
					couple of weeks to see the finished one.
				</p>
			</div>
		</CSSTransition>,
	];

	useEffect(() => {
		if (gridContainerRef.current) {
			// // IE9, Safari, Chrome Opera
			// gridContainerRef.current.addEventListener(
			// 	"mousewheel",
			// 	handleMouseScroll,
			// 	false
			// );
			// // Firefox
			// gridContainerRef.current.addEventListener(
			// 	"DOMMouseScroll",
			// 	handleMouseScroll,
			// 	false
			// );
			gridContainerRef.current.addEventListener(
				"mousewheel",
				handleMouseScroll,
				false
			);
			gridContainerRef.current.addEventListener(
				"DOMMouseScroll",
				handleMouseScroll,
				false
			);
			gridContainerRef.current.addEventListener(
				"wheel",
				handleMouseScroll,
				false
			);
		}

		return () => {
			if (gridContainerRef.current) {
				gridContainerRef.current.removeEventListener(
					"mousewheel",
					handleMouseScroll
				);
				gridContainerRef.current.removeEventListener(
					"DOMMouseScroll",
					handleMouseScroll
				);
				gridContainerRef.current.removeEventListener(
					"wheel",
					handleMouseScroll
				);
			}
		};
	}, []);

	function handleMouseScroll(e) {
		// console.log(e);
		e.preventDefault();
		deltaYSum = deltaYSum + e.deltaY;
		if (!timer) {
			timer = setTimeout(() => {
				if (deltaYSum > req_scroll_delta) onMouseScrollDown();
				if (deltaYSum < -req_scroll_delta) onMouseScrollUp();
				timer = null;
				deltaYSum = 0;
			}, 750);
		}
	}

	useEffect(() => () => clearTimeout(timer), []);

	function onMouseScrollDown() {
		console.log("onMouseScrollDown");
		goToNextPage();
	}

	function onMouseScrollUp() {
		console.log("onMouseScrollUp");
		goToPrevPage();
	}

	function goToNextPage() {
		setSelectedPage((selectedPage) => {
			if (selectedPage + 1 >= pages.length) return selectedPage;
			else return selectedPage + 1;
		});
	}

	function goToPrevPage() {
		setSelectedPage((selectedPage) => {
			if (selectedPage - 1 < 0) return selectedPage;
			else return selectedPage - 1;
		});
	}

	const isTouchDevice = window.matchMedia("(pointer: coarse)").matches;
	let tmpSwipeHandler = useSwipeable({
		onSwipedUp: () => goToNextPage(),
		onSwipedDown: () => goToPrevPage(),
	});
	// avoid problem that mousescroll events would not be called if swipeHanlders are active
	const swipeHandlers = isTouchDevice ? tmpSwipeHandler : null;

	return (
		<Grid
			container
			className="home"
			ref={gridContainerRef}
			{...props}
			{...swipeHandlers}
		>
			<Grid item sm={6} className="home-grid-item"></Grid>
			<Grid item sm={6} className="home-grid-item">
				<TransitionGroup>{pages[selectedPage]}</TransitionGroup>
			</Grid>

			<PageIndicator
				style={{
					position: "absolute",
					left: 32,
					bottom: 32,
					zIndex: 1000,
				}}
				counts={pages.length}
				current={selectedPage}
			/>
			<Fade in={selectedPage > 0}>
				<ScrollUpIcon
					onClick={goToPrevPage}
					style={{
						cursor: "pointer",
						position: "absolute",
						left: "50%",
						right: "50%",
						top: 86,
					}}
				/>
			</Fade>
			<Fade in={selectedPage < pages.length - 1}>
				<ScrollDownIcon
					onClick={goToNextPage}
					style={{
						cursor: "pointer",
						position: "absolute",
						left: "50%",
						right: "50%",
						bottom: 16,
					}}
				/>
			</Fade>
		</Grid>
	);
}
